
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Range from 'vux/src/components/range/index.vue'
import Box from 'vux/src/components/box/index.vue'

import { mapState, mapMutations, mapActions } from "vuex";

export default {
name: "settings",
components: {
Group,
GroupTitle,
XSwitch,
XNumber,
XButton,
Radio,
PopupRadio,
Cell,
Range,
Box
},
data: function() {
return {
showLanguageOption: false
};
},
computed: {
...mapState("settings", [
"language",
"thinkTimeOption",
"turnTime",
"matchTime",
"maxDepth",
"nbest",
"configIndex",
"boardSize",
"rule",
"aiThinkBlack",
"aiThinkWhite",
"clickCheck",
"showCoord",
"showDetail",
"showMultiPvEval",
"showIndex",
"showLastStep",
"showWinline",
"boardStyle"
]),
languageOptions() {
return [
{ key: "cn", value: this.$t("setting.language.cn") },
{ key: "en", value: this.$t("setting.language.en") }
];
},
thinkTimeOptions() {
return [
{ key: 1, value: this.$t("setting.thinking.fast") },
{ key: 2, value: this.$t("setting.thinking.slow") },
{ key: 3, value: this.$t("setting.thinking.analysis") },
{ key: 0, value: this.$t("setting.thinking.custom") }
];
},
thinkStyleOptions() {
return [
{ key: 0, value: this.$t("setting.thinking.style.default") },
{ key: 1, value: this.$t("setting.thinking.style.conservative") },
{ key: 2, value: this.$t("setting.thinking.style.elegant") }
];
},
ruleOptions() {
return [
{ key: 0, value: this.$t("setting.board.rule.gomoku") },
{ key: 5, value: this.$t("setting.board.rule.swap1") }
];
},
clickCheckOptions() {
return [
{ key: 0, value: this.$t("setting.board.clickCheck.direct") },
{ key: 1, value: this.$t("setting.board.clickCheck.confirm") },
{ key: 2, value: this.$t("setting.board.clickCheck.slide") }
];
},
languageValue: {
get() {
return this.language;
},
set(v) {
this.setValue({ key: "language", value: v });
}
},
thinkTimeSelected: {
get() {
return this.thinkTimeOption;
},
set(v) {
this.setValue({ key: "thinkTimeOption", value: v });
}
},
turnTimeInSecond: {
get() {
return Math.floor(this.turnTime / 500) / 2;
},
set(v) {
this.setValue({ key: "turnTime", value: v * 1000 });
}
},
matchTimeInSecond: {
get() {
return Math.floor(this.matchTime / 1000);
},
set(v) {
this.setValue({ key: "matchTime", value: v * 1000 });
}
},
maxDepthValue: {
get() {
return this.maxDepth;
},
set(v) {
this.setValue({ key: "maxDepth", value: v });
}
},
nbestValue: {
get() {
return this.nbest;
},
set(v) {
this.setValue({ key: "nbest", value: v });
}
},
thinkStyleValue: {
get() {
return this.configIndex;
},
set(v) {
this.setValue({ key: "configIndex", value: v });
}
},
boardSizeValue: {
get() {
return this.boardSize;
},
set(v) {
this.setValue({ key: "boardSize", value: v });
}
},
ruleValue: {
get() {
return this.rule;
},
set(v) {
this.setValue({ key: "rule", value: v });
}
},
aiThinkBlackValue: {
get() {
return this.aiThinkBlack;
},
set(v) {
this.setValue({ key: "aiThinkBlack", value: v });
}
},
aiThinkWhiteValue: {
get() {
return this.aiThinkWhite;
},
set(v) {
this.setValue({ key: "aiThinkWhite", value: v });
}
},
clickCheckValue: {
get() {
return this.clickCheck;
},
set(v) {
this.setValue({ key: "clickCheck", value: v });
}
},
showCoordValue: {
get() {
return this.showCoord;
},
set(v) {
this.setValue({ key: "showCoord", value: v });
}
},
showDetailValue: {
get() {
return this.showDetail;
},
set(v) {
this.setValue({ key: "showDetail", value: v });
}
},
showMultiPvEvalValue: {
get() {
return this.showMultiPvEval;
},
set(v) {
this.setValue({ key: "showMultiPvEval", value: v });
}
},
showIndexValue: {
get() {
return this.showIndex;
},
set(v) {
this.setValue({ key: "showIndex", value: v });
}
},
showLastStepValue: {
get() {
return this.showLastStep;
},
set(v) {
this.setValue({ key: "showLastStep", value: v });
}
},
showWinlineValue: {
get() {
return this.showWinline;
},
set(v) {
this.setValue({ key: "showWinline", value: v });
}
},
pieceScale: {
get() {
return this.boardStyle.pieceScale * 100;
},
set(v) {
this.setBoardStyle({ key: "pieceScale", value: v * 0.01 });
this.setBoardStyle({ key: "indexScale", value: v * 0.0046875 });
}
},
boardColor: {
get() {
return this.boardStyle.boardColor;
},
set(v) {
this.setBoardStyle({ key: "boardColor", value: v });
}
},
lastStepColor: {
get() {
return this.boardStyle.lastStepColor;
},
set(v) {
this.setBoardStyle({ key: "lastStepColor", value: v });
}
},
winlineColor: {
get() {
return this.boardStyle.winlineColor;
},
set(v) {
this.setBoardStyle({ key: "winlineColor", value: v });
}
},
bestMoveColor: {
get() {
return this.boardStyle.bestMoveColor;
},
set(v) {
this.setBoardStyle({ key: "bestMoveColor", value: v });
}
},
thinkingMoveColor: {
get() {
return this.boardStyle.thinkingMoveColor;
},
set(v) {
this.setBoardStyle({ key: "thinkingMoveColor", value: v });
}
},
thoughtMoveColor: {
get() {
return this.boardStyle.thoughtMoveColor;
},
set(v) {
this.setBoardStyle({ key: "thoughtMoveColor", value: v });
}
},
lostMoveColor: {
get() {
return this.boardStyle.lostMoveColor;
},
set(v) {
this.setBoardStyle({ key: "lostMoveColor", value: v });
}
}
},
methods: {
...mapMutations("settings", ["setValue", "setBoardStyle"]),
...mapActions("settings", ["clearCookies"]),
reset() {
this.clearCookies();
location.reload();
}
},
watch: {}
};
