var workerURL = process.env.BASE_URL + 'build/rapfi-worker.js'
var worker, I8AB, stopAddress;
var callback;

function spawnWorker() {
    worker = new Worker(workerURL);
    I8AB = null;
    stopAddress = null;
    worker.onmessage = function (e) {
        let data = e.data
        if (data.buffer) {
            I8AB = new Int8Array(data.buffer)
            return
        } else if (data.stopAddr) {
            stopAddress = data.stopAddr
            return
        } else if (data.ready) {
            callback({ ok: true })
        } else {
            processOutput(data.output)
        }
    }
    worker.onerror = function () {
        worker.terminate()
        console.warn("Memory allocation failed... Retry after 200ms...")
        setTimeout(spawnWorker, 200)
    }
}

function setCallback(f) {
    callback = f
    spawnWorker()
}

function forceStop() {
    worker.terminate()
    spawnWorker()
}

function stopThinking() {
    if (typeof Atomics === "undefined" || !I8AB) {
        console.warn("no support for SAB!")
        throw "no support for SAB"
    }
    if (!stopAddress) {
        console.warn("haven't got stop variable address!")
        throw "no stop variable address"
    }

    // 直接设置终止变量：bool terminate;
    // eslint-disable-next-line
    Atomics.store(I8AB, stopAddress, 1)
}

function sendCommand(cmd) {
    if (typeof cmd !== "string" || cmd.length == 0)
        return
    worker.postMessage(cmd)
}

function processOutput(output) {
    if (typeof callback !== "function")
        return
    
    let i = output.indexOf(' ')

    if (i == -1) {
        if (output == "OK")
            callback({ ok: true })
        else if (output == "SWAP")
            callback({ swap: true })
        else {
            let coord = output.split(',')
            callback({ pos: [+coord[0], +coord[1]] })
        }
        return
    }

    let head = output.substring(0, i)
    let tail = output.substring(i + 1)

    if (head == "MESSAGE") {
        if (tail.startsWith("REALTIME")) {
            let r = tail.split(' ')
            let coord = r[2].split(',')
            callback({
                realtime: {
                    type: r[1],
                    pos: [+coord[0], +coord[1]]
                }
            })
        } else {
            callback({ msg: tail })
        }
    } else if (head == "DEPTH")
        callback({ depth: tail })
    else if (head == "EVAL")
        callback({ eval: tail })
    else if (head == "BESTLINE")
        callback({
            bestline: tail.split(' ').map(s => {
                let coord = s.match(/\[([A-Z]),(\d+)\]/)
                let x = coord[1].charCodeAt(0) - 'A'.charCodeAt(0)
                let y = +coord[2] - 1
                return [x, y]
            })
        })
    else if (head == "NODES")
        callback({ nodes: +tail })
    else if (head == "SPEED")
        callback({ speed: +tail })
    else if (head == "MULTIPV")
        callback({ multipv: tail })
    else if (head == "ERROR")
        callback({ error: tail })
    else
        callback({ unknown: tail })
}

export {setCallback, sendCommand, stopThinking, forceStop}